import React from 'react'
import Link from 'gatsby-link'

const IndexPage = () => (
  <div>
    <h1>Welcome to Adventure Heads</h1>
    <p>This is a site to show all of my adventures</p>
    <Link to="/blog">Check out the Blog page!</Link>
  </div>
)

export default IndexPage



